body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: beige;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}
h1{
  color: cornflowerblue;
}
h2{
  color: red;
}
ul{
  list-style: none;
  padding: 10px;
  margin: 0 auto 10px;
  background-color: rgb(213, 248, 246);
}
p{
  text-align: left;
  width: 200px;
  margin-left: 35%;
}
.row, form{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 30px;
  align-items: center;
}
.row input{
  width: 70%;
  padding-left: 5px;
  height: 50px;
  font-size: 1.4rem;
}
.login input{
  width: 100%;
  display: block;
  margin-bottom: 20px;
}

.login-btns{
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.login-btns button{
  margin-left: 20px;
  width: 200px;
  background-color: lightskyblue;
  color: white;

}
.login-btns button:hover{
  background-color: rgba(135, 206, 250, 0.49);
  color: black;
  cursor: pointer;
}
input.check_box{
  width: 40px;
  margin-right: 10px;
}

.row span{
  width: 60%;
  flex-grow: 1;
  text-align: left;
  font-size: 1.4rem;
  text-transform: uppercase;
}
.row button{
  width: 160px;
  height: 50px;
  margin-bottom: 10px;
  font-size: 1.1rem;
  text-transform: uppercase;
  border: 1px solid cornflowerblue;
}
.row button:hover{
  background-color: cornflowerblue;
  color: white;
  cursor: pointer;
}
.loader{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.756);
  display: flex;
  justify-content: center;
  align-items: center;
}
.lds-dual-ring {
  display: block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #000;
  border-color: #000 transparent #000 transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 800px) {
  .login-btns{
    flex-direction: column;
    justify-content: end;
    align-items: end;
  }
}